export  function  marketShare (htvl,contributions) {

  const tvl      = htvl,
        contribs = contributions;

  if (tvl && contribs) {

    const res = tvl.map((i,m) => ({
      date : i?.date,
      val : ((tvl[m]?.val/contribs[m]?.val)*100)
    }));
      
    return res
  }

}
