import { useTheme } from 'styled-components';
import { Markets } from '../../components/marketDominance/markets'
import {Porcent} from '../../data/format';
import styled from 'styled-components';
import _ from "lodash"

const Blocky = styled.div`
  min-width:5px;
  height:24px;
  background:#ffefef;
  overflow:hidden;
  &:first-of-type{
    border-radius:5px 0px 0px 5px;
  }
`;

function Block({api, marketshare, raised}) {
  const theme = useTheme();

  const crowdloan = Markets?.filter(a => a.id === marketshare?.id)[0];
  const market  = _.filter(marketshare?.data, function(o) {
    if(Object.entries(o)[0][0] === api) { return o }
  });
  return (
    <>
      { market?.length > 0 &&
        <Blocky key={marketshare?.id} className={marketshare?.id} 
          style={{
            width:Porcent((market[0][api]/raised))+'%',
            backgroundColor: crowdloan?.color === '#0c3ee3' ? theme?.colors?.main : crowdloan?.color
          }} >
        </Blocky>
      }
    </>
  )
}

export default Block;