import React from 'react';
import styled from 'styled-components';

const width = '25px';

const Button = styled.button`
  position:relative;
  margin-left:auto;
  background:transparent;
  border:0px;
  cursor:pointer;
  border-radius:50%;
  width:${width};
  height:${width};
  border:0px;
  outline:0px;
  &:after,&:before {
    position:absolute;
    content:'';  
    top:0px;
    height:100%;
  }
  &:after {
    top:2px;
    left:65%;
    width:50%;
    height:100%;
    background-color:${({ theme }) => theme.colors.text};
    border-radius:0px 20px 20px 0px;
    z-index:1;
  }

  &:before {
    border-radius:${width};
    width:${width};
    height:${width};
    left:0px;
    border-width:2px;
    border-style:solid;
    border-color:${({ theme }) => theme.colors.text};
    z-index:2;
  }

`
const ThemeToggler = (props) => {
   const { themeToggler } = props;
   return (
      <Button onClick={themeToggler}/>
   );
};

export default ThemeToggler;