import { Link } from "react-router-dom";
import styled from 'styled-components';
import Polkadot from '../../assets/images/dot.svg'
import Kusama from '../../assets/images/ksm.svg'
import { useLocation } from "react-router-dom"

const Img = styled.img`
  filter:${({ theme }) => theme.effect};
`;

const List = styled.li`
  display:inline-block;
  vertical-align:middle;
  a {
    display:block;
    padding:${({ theme }) => theme.padding.middle};
    border-radius: ${({ theme }) => theme.radius};
    box-shadow: ${({ theme }) => theme.shadow};
    &.active {
      background: ${({ theme }) => theme.colors.bg};
    }
  }
`;

function Network() {
  const location = useLocation()?.pathname;
  return (
    <nav>
      <ul>
        <List>
          <Link to="polkadot" className={location.includes('polkadot') ? 'active' : '' }>
            <Img src={Polkadot} alt="Polkadot Network" />
          </Link>
        </List>
        <List>
          <Link to="kusama" className={location.includes('kusama') ? 'active' : '' }>
            <Img src={Kusama} alt="Kusama Network" />
          </Link>
        </List>
      </ul>
    </nav>
  )
}

export default Network;