import styled from 'styled-components';
import Network from '../../api/network';
import dot from '../../assets/images/placeholder/dot.svg';
import ksm from '../../assets/images/placeholder/ksm.svg';

const ImageBox = styled.div`
  background:${({ theme }) => theme.colors.icon};
  border-radius:50%;
  padding:3px;
`;

const Image = styled.img`
  display:block;
  border-radius:50%;
  width:40px;
  height:40px;
`;

function Logo({api, parachain}) {
  const img         = parachain?.filter(i => i.paraid === api),
        placeholder = Network() === 'kusama' ? ksm : dot,
        alt         = img?.length ? img[0]?.name : api,
        url         = img?.length ? img[0]?.icon : placeholder;

  return (
    <ImageBox>
      <Image title={alt} src={url}/>
    </ImageBox>
  )
}

export default Logo;