import {useState, useEffect} from 'react'
import axios from 'axios'


const  Api = (url) => {
  const [data, setData] = useState(null)

  const getData = () => {
    axios.get(url)
    .then((res) => {
      setData(res)
    }).catch((error)=> {
      console.log(error)
    })
  }
  useEffect((url) => {
    getData();
  },[])

  return data
  
}

export default Api
