import ProjectDetail from './projectDetail'

function ProjectsList({api, parachain, marketshare}) {
  const data = api;
  return (
    data.map((item, key) => {
      return (
        <ProjectDetail key={key} api={item} parachain={parachain} marketshare={marketshare?.data} globalTvl={data[0].raised}/>
      )
    })
  )
}

export default ProjectsList;