const size = {
  smallPhones : '400px',
  mobile: '767px',
  tablet: '1199px',
  desktop: '1200px'
}
export const device = {
  mobile: `(max-width: ${size.mobile})`,
  tablet: `(max-width: ${size.tablet})`,
  desktop: `(min-width: ${size.desktop})`,
};


const paddings = {
  default :'15px 30px',
  middle  :'15px'
}

const margins = { default :'25px 0px'}

const fonts = {
  nav     : '18px',
  title   : '19px',
  titles  : '35px',
  smallT  : '26px',
  big     : '40px',
  family  : 'DM Sans, sans-serif'
}
const contents = { size    : '1200px' }

const radius = '12px';

const shadows = '0 0 16px rgb(58 78 88 / 5%)';

export const lightTheme = {
  name   : 'light',
  effect : 'invert(0)',
  colors: {
     main         : "#0c3ee3",
     text         : "#222",
     light        : "#ccc",
     green        : "#1da63b",
     red          : "#f22c31",
     bg           : '#fff',
     icon         : '#f8f8f8',
     background   : '#f8f8f8',
     status       : ['#ddf2e2','#1da63b'],
     preload      : ['#eee','#ddd', '#5e6d7c']
  },

  content:contents,
  padding:paddings,
  margin:margins,
  font:fonts,
  radius:radius,
  shadow:shadows
}

export const darkTheme = {
  name   : 'dark',
  effect : 'invert(1)',
  colors: {
     main         : "#fc077d",
     text         : "#fff",
     light        : "#ccc",
     green        : "#1da63b",
     red          : "#f22c31",
     bg           : '#222222',
     icon         : '#313131',
     background   : '#000000',
     status       : ['#313131','#1da63b'],
     preload      : ['#373737','#474747', '#5e6d7c']
  },

  content:contents,
  padding:paddings,
  margin: margins,
  font:fonts,
  radius:radius,
  shadow:shadows
}