import { Flex } from '../../assets/global'
import Bar from './bar.jsx'
import Raised from './raised.jsx'
import styled from 'styled-components';

const Container = styled(Flex)`
  width:100%;
  position:relative;
`

function ProgressBar({api, marketshare, globalTvl}) {
  const data = api;

  return (
    <Container>
      <Bar api={data?.id} raised={data?.raised} marketshare={marketshare} globalTvl={globalTvl} />
      <Raised api={data?.raised} raised={data?.parallelRaised}/>
    </Container>
  )
}

export default ProgressBar;