import TableItem from './tableItem'
import styled from 'styled-components';
import {device} from '../../assets/theme'

const TableContainer = styled.div`
  display: block;
  margin-top: 10px;
  max-height: 198px;
  margin-right:-30px;
  margin-left:-5px;
  overflow-y: scroll;
  padding-right:10px;
  @media ${device.tablet} {
    max-height:208px;
  }

  &::-webkit-scrollbar {
    width: 5px;
    margin-right:-10px;
  }
 
  &::-webkit-scrollbar-track {
    background-color: ${({ theme }) => theme.colors.bg};
  }
  
  &::-webkit-scrollbar-thumb {
    background:${({ theme }) => theme.colors.icon};
    border-radius:10px;
  }

  @media ${device.tablet} {
    margin-right:-25px;
    padding-right:10px;
  }
`
const TableScroll = styled.div`
  padding-right:10px;
  padding-top:5px;
`;

function Table({api, unit , parachain, totalSupply, crowdloanRaised}) {
  return (
    <TableContainer style={{maxHeight: unit === 'DOT' ? '198px' : '209px'}}>
      <TableScroll>
        { 
          api?.map((item, key) => { 
            const parachains = parachain?.filter(i => i.paraid === item.paraId)[0];
            return <TableItem key={key} api={item} unit={unit} parachain={parachains} totalRaised={crowdloanRaised} totalSupply={totalSupply}/>
          })
        }
      </TableScroll>
    </TableContainer>
  )
}

export default Table;