
import _ from "lodash";
import React, { useState, useEffect } from 'react';
import { Box, Title } from '../../assets/global'
import Featured from '../charts/featured'
import ChartBar from '../charts/chartBar'
import ChartBarLoader from '../charts/chartBarLoader'
import FilterByProject from '../dailyOnCrowdloan/filterByProject'

function DailyOnCrowdloan({unit, api, parachain, rate}) {
  const [type, setType ] = useState(unit === 'DOT' ? 'Parallel Finance' : 'Parallel Heiko');

  //Toupdate on auction-servics or api

  const dailyDotsData = api?.map((entry, index) => (
    _.mapKeys(entry, function(value, key) {
      let keys = key?.substring(0,4);
      return keys === 'date' ? keys : parachain?.find(x => x.paraid === keys?.toString())?.name
    })
  ))

  const data = dailyDotsData?.map((entry, index) => (
    Object.fromEntries(
      Object.entries(entry)?.map(([k, v]) => {
          return k === 'date' || index === 0 ?
            [k,v] :
            (v - parseInt(dailyDotsData[index-1][k])) < 1 ? [k, 0] :
            [k, (v - parseInt(dailyDotsData[index-1][k]))] 
      })
    ))
  )?.map(function (el) {
    if(el[type] !== 0) {
      return { ...el, val  : el[type],
    }
    }
  })?.filter(x => x)


  const featured  = !data ? [] : data[data?.length-1];

  return !data ? <ChartBarLoader/> : (
    <Box className="full">
      <FilterByProject type={type} parachain={parachain} setType={setType} />
      <Title>Crowdloan: Daily Contributions ({type})</Title>
      <Featured data={featured} unit={unit}/>
      <ChartBar data={data} unit={unit} type="small"/>
    </Box>
  )
}

export default DailyOnCrowdloan;