import { AreaChart , Area , XAxis , Tooltip , ResponsiveContainer} from 'recharts';
import Legend from './legend'

const Chart = ({ data, unit }) => {
  return(
    <ResponsiveContainer width="100%" height={200}>
      <AreaChart width={600} height={200} data={data} margin='0'>       
        <XAxis dataKey="date" interval={30}  textAnchor="left" />
        <Tooltip content={<Legend unit={unit}/>} />
        <Area type="monotone" dataKey="val" strokeWidth="3" stroke={"#0C3EE3"} dot={false} animationDuration={250} fill="none" />
      </AreaChart>
    </ResponsiveContainer> 
  )
}
export default Chart;