import { useTheme } from 'styled-components';
import { PieChart, Pie, Tooltip, Cell, ResponsiveContainer } from 'recharts';
import { Subtitle , Small } from '../../assets/global';
import { Markets } from '../../components/marketDominance/markets'
import { m,big } from '../../data/format';
import styled from 'styled-components';

const Container = styled.div`
  position:relative;
`;

const Title = styled(Subtitle)`
  position:absolute;
  padding:0px;
  text-align:center;
  display:block;
  top:50%;
  left:50%;
  transform:translate(-50%,-50%);
  small {
    display:block;
  }
  
`

function ChartPie({api,globalTvl,crowdloanTvl}) {
  const theme = useTheme();
  const othersValue   = big((globalTvl-big(crowdloanTvl)),true);
  const apis          = !api ? [] : [{totalValue:othersValue, id:'others'},...api ];
  return (
    <>
      <Container>
        <Title>
          {m(big(globalTvl,true))}
          <Small>DOT</Small>
        </Title>
        
        <ResponsiveContainer width="100%" height={300}>
          <PieChart >
          <Pie
              data={apis}
              innerRadius={105}
              outerRadius={120}
              cornerRadius={50}
              paddingAngle={1}
              minAngle={8}
              isAnimationActive={false}
              dataKey="totalValue"
            >
              <Tooltip />
              {Markets?.map((entry, index) => (
                <Cell key={`cell-${index}`} stroke="none" fill={entry.color === '#0c3ee3' ? theme?.colors?.main : entry.color} />
              ))}
            </Pie>
          </PieChart>  
        </ResponsiveContainer>
      </Container>
    </>
  )
}

export default ChartPie;