import Api from '../api'
import Network from '../network'

export  function  parachains () {
  const parachans_api = Network() === 'kusama' ? 
  process.env.REACT_APP_KUSAMA_PARACHAINS
  : 
  process.env.REACT_APP_POLKADOT_PARACHAINS;
  
  const res = Api(parachans_api)
  
  if(res?.data) {
    return res.data
  }

}