import { Box, Title } from '../../assets/global'
import List from '../marketDominance/list'
import styled from 'styled-components';
import { device } from "../../assets/theme";
import MarketDominancePreloader from '../marketDominance/marketDominancePreloader'

const Boxs = styled(Box)`
  max-width:35% !important;
  @media ${device.tablet} {
    max-width:100% !important;
  }
`;

function MarketDominance({api, globalTvl}) {
  return !api || !globalTvl ? <MarketDominancePreloader/> : (
    <Boxs>
      <Title>Market Dominance</Title>
      <List api={api} globalTvl={globalTvl} />
    </Boxs>
  )
}

export default MarketDominance;