import { useTheme } from 'styled-components';
import {m,big, Porcent} from '../../data/format';
import styled from 'styled-components';

const Text = styled.p`
  width:100%;
  display:block;
  color:${({ theme }) => theme.colors.text};
  padding: 8px 0px;
`
const Circle = styled.span`
  width: 12px;
  height: 12px;
  display: inline-block;
  border-radius: 50%;
  margin-right: 5px;
`

const Amount = styled.strong`
  font-weight:bold;
  display:inline-block;
`

function Crowdloan({api, markets, globalTvl, crowdloanTvl}) {
  const theme = useTheme();
  //To update on auction-services
  const data          = api,
        othersValue   = big((globalTvl-big(crowdloanTvl)),true),
        value         = data ? data.totalValue : othersValue,
        porcent       = value/big(globalTvl,true)
  //end to update on auction-services
  return (
    <Text> 
      <Circle style={{background: markets.color === '#0c3ee3' ? theme?.colors?.main : markets.color}}/>
      {`${markets.name} : `}
      <Amount> {`${m(value)} (${Porcent(porcent)}%) `} </Amount>
    </Text>
  )
}



export default Crowdloan;