import Api from '../api'
import _ from "lodash"
import Network from '../../api/network';

export  function  contributions () {
  const contribution_api = Network() === 'kusama' ? 
  process.env.REACT_APP_KUSAMA_CONTRIBUTIONS
  : 
  process.env.REACT_APP_POLKADOT_CONTRIBUTIONS;
  
  const res  = Api(contribution_api)
  const done = res?.data?.stats;

  /* To update on Auction-service */

  const original = done?.map(function(data, i){
    return {
      ...data,
      date: data?.date?.substring(0,6),
    }
  })

  const contributions = original?.map(function(data, i){
    return {
      date: data?.date,
      val : _.sum(_.values(_.filter(done[i], x => typeof x === 'number'))),
    }
  })

  if(original) {
    return {
      original: original,
      contributions: contributions
    }
  }

}
