import {Porcent} from '../../data/format';
import styled from 'styled-components';
import { Flex } from '../../assets/global'
import { device } from "../../assets/theme"
import Block from './block'

const Blocks = styled(Flex)`
  width:100%;
  max-width:100%;
  margin:0px 20px;
  @media ${device.tablet} {
    margin:10px 0px;
  } 
`

const BlockContainer = styled.div`
  max-width:100%;
  width:100%;
  display:flex;
  border-radius:5px;
  height:25px;
  background:${({ theme }) => theme.colors.icon};
  min-width:50px;

`

function Bar({api, marketshare, raised, globalTvl}) {
  const realPorcent = Porcent((raised/globalTvl)) + '%';

  return (
    <>
    <Blocks>
      <BlockContainer style={{width:realPorcent}}>
        { 
          marketshare?.map((item) => {
            return (
              <Block key={item.id} api={api} marketshare={item} raised={raised}/>
            )
          })
        }
      </BlockContainer>
    </Blocks>
    </>
  )
}

export default Bar;