import { Markets } from '../../components/marketDominance/markets'
import Crowdloan from './crowdloan'
import ChartPie from './chartPie'
import styled from 'styled-components';

const Labels = styled.div`
  position:relative;
  padding:15px 0px;
  text-align:center;
  p {
    display:inline-block;
    max-width:230px;
    text-align:left;
  }
`;

function List({api,globalTvl}) {
  
  return (
    <>
      <ChartPie api={api?.data} globalTvl={globalTvl} crowdloanTvl={api?.totalValue} crowdloanTvl={api?.totalValue}/>
      <Labels>
        {Markets?.map((entry, index) => {
          const crowdloan_data = api?.data.filter(x => x.id === entry.id)[0]
          return(
            <Crowdloan key={index} api={crowdloan_data} markets={entry} globalTvl={globalTvl} crowdloanTvl={api?.totalValue}/>
          )
        })}
      </Labels>
    </>
  )
}

export default List;