import styled from 'styled-components';
import { device } from "./theme"

const Containers = styled.div`
  max-width: ${({ theme }) => theme.content.size};
  margin:0px auto;
`
const Titles = styled.h2`
  font-size: ${({ theme }) => theme.font.title};
  color: ${({ theme }) => theme.colors.text};
  padding: ${({ theme }) => theme.padding.middle} 0px;
  font-weight: bold; 
`
const Subtitles = styled.h3`
  font-size: ${({ theme }) => theme.font.big};
  color: ${({ theme }) => theme.colors.text};
  padding: 0px 0px ${({ theme }) => theme.padding.middle} 0px;
  font-weight:bold;
  @media ${device.mobile} {
    font-size: ${({ theme }) => theme.font.smallT};
  } 
`
const Smalls = styled.small`
  font-size:50%;
  color:${({ theme }) => theme.colors.light};
  @media ${device.mobile} {
    display:block;
    font-size:65%;
    padding-top:5px;
  }
`
const Boxes = styled.div`
  position:relative;
  width:100%;
  max-width:48%;
  background:${({ theme }) => theme.colors.bg};
  margin:${({ theme }) => theme.margin.default};
  border-radius:${({ theme }) => theme.radius};
  padding:${({ theme }) => theme.padding.default};
  box-shadow:${({ theme }) => theme.shadow};
  &.full {
    max-width:100%;
  }
  @media ${device.tablet} {
    max-width:100%;
  }
`
const Flexes = styled.div`
  display:flex;
  align-items:center;
  &.spacing {
    justify-content: space-between;
  }
  @media ${device.tablet} {
    flex-wrap:wrap;
  }
`

const Grids = styled.div`
  max-width:61%;
  width:100%;
  > div {
    max-width:100% !important;
    &:first-of-type{
      + div {
        margin-top:40px;
      }
    }
  }
  @media ${device.tablet} {
    max-width:100%;
  }

`

export const Grid = ({ children, className}) => {
  return <Grids className={className}>{ children }</Grids>;
};

export const Container = ({ children }) => {
  return <Containers>{ children }</Containers>;
};

export const Flex = ({ children, className }) => {
  return <Flexes className={className}>{ children }</Flexes>;
};

export const Box = ({ children, className }) => {
  return <Boxes className={className}>{ children }</Boxes>;
};

export const Title = ({ children }) => {
  return <Titles>{ children }</Titles>;
};

export const Subtitle = ({ children, className }) => {
  return <Subtitles className={className}>{ children }</Subtitles>;
};

export const Small = ({ children }) => {
  return <Smalls>{ children }</Smalls>;
};