import Api from './api'

export  function  rate (network) {
  
  const rateApi   = network === 'DOT' ? process.env.REACT_APP_POLKADOT_RATE : process.env.REACT_APP_KUSAMA_RATE;
  const today     = new Date().getTime();
  const startDate = network === 'DOT' ? new Date('2021-11-05').getTime() : new Date('2021-06-09').getTime();
  const diff      = ((today - startDate)/(1000*60*60*24)).toFixed(0);

  const res  = Api(rateApi+diff)
  const data = res?.data?.Data?.Data?.map(function(item, i){
    return { 
      val: item?.close,
    }
  });

  if(data) {
    return data
  }
}
