import Api from '../api'

const {REACT_APP_HISTORICALTVL} = process.env

export  function  htvl () {
  const res = Api(REACT_APP_HISTORICALTVL)
  
  /*To Remplace on auction service*/
  if(res) {
    const original = Object.entries(res.data)?.map(function(item, i){
    return { 
      date: item[0].substring(0,6),
      val : item[1]/10000000000,
    }
  })

    const tvl = original?.reduce((r, value, i) => {
      const { val, date } = value
      const cumVal = i === 0 ? 0 : r[i - 1].val
      const newDatum = { date, val: val + cumVal }
      r.push(newDatum);
      return r;
    }, [])?.map(function(item, i){
      return { 
        date : item.date,
        val : item.val,
        //usd : item.val*dotPrice[i].close,
      }
    })

    return {
      original: original,
      tvl: tvl
    }
  }

}
