import React, {useState} from 'react';
import { Box, Title } from '../../assets/global'
import Featured from '../charts/featured'
import Chart from '../charts/chart'
import ChartLoader from '../charts/chartLoader';
import Rate from '../rates'


function CrowdloanTvl({unit, api , rate}) {
  const[rates, setRates]=useState(unit);

  const apiUSD = !rate ? api : api?.map(function(item, i){
    return {
      ...item,
      val : item?.val*rate[i]?.val
    }
  })

  const data = rates === unit ? api : apiUSD;
  const featured  = !data ? [] : data[data?.length-1];

    return !data ? <ChartLoader/> : (
    <Box>
      <Rate token={unit} changeRate={setRates}/>
      <Title>Crowdloan {unit} TVL</Title>
      <Featured data={featured} unit={rates}/>
      <Chart data={data} unit={rates}/>
    </Box>
  )
}

export default CrowdloanTvl;