import Network from'./network';
import Links from'./links';
import { Flex } from '../../assets/global'
import ThemeToggler from "../../assets/themeToggle";

function Nav({themeToggler}) {
  return (
    <>
      <Flex>
        <Network />
        <Links />
        <ThemeToggler themeToggler={themeToggler} />
      </Flex>
    </>
  )
}

export default Nav;