import { Box } from '../../assets/global'
import styled, { keyframes } from 'styled-components'
import { device } from "../../assets/theme";

const Boxs = styled(Box)`
  max-width:35% !important;
  @media ${device.tablet} {
    max-width:100% !important;
  }
`;

const width   = '600px',
      height  = '300px';

const animate = keyframes`
 0%   { background-position: -${width} 0;  }
 100% { background-position: ${width} 0;   }
`

const Preloader = styled.div`
  animation: 1.5s forwards infinite ${animate} linear;
  clip-path: url(#clip-dominance);
  -webkit-clip-path: url(#clip-dominance);
  background: linear-gradient(to right, ${({ theme }) => theme.colors.preload[0]} 8%, ${({ theme }) => theme.colors.preload[1]} 18%, ${({ theme }) => theme.colors.preload[0]} 33%);
  background-size: ${width} ${height};
  height: 651px;
  -webkit-transform:translateZ(0);

`;

const Svg = styled.svg`
width:0px;
height:0px;
@media ${device.mobile} {
  rect {
    x:45px;
    &:first-of-type{
      x:0px;
    }
  }

  circle {
      cx : 160px;
    }
  }
  
} 
`

const Rect = styled.rect`
`

const MarketDominancePreloader = () => {
  return(
    <Boxs>
      <Svg height="0" width="0" aria-hidden="true">
        <clipPath id="clip-dominance">
          <Rect y="0"   width="200" height="17"/>
          <circle  cx="180" cy="180" r="115"></circle>
          
          <Rect y="335" x="65" width="230" height="23"/>                           
          <Rect y="370" x="65" width="230" height="23"/>   
          <Rect y="405" x="65" width="230" height="23"/>                           
          <Rect y="440" x="65" width="230" height="23"/>       
          <Rect y="475" x="65" width="230" height="23"/>   
          <Rect y="510" x="65" width="230" height="23"/>                           
          <Rect y="545" x="65" width="230" height="23"/>    
          <Rect y="580" x="65" width="230" height="23"/>                    
          <Rect y="615" x="65" width="230" height="23"/>                    
        </clipPath>
      </Svg>
      <Preloader/>
    </Boxs>
  )
}

export default MarketDominancePreloader;