import React, { useState } from 'react';
import styled from 'styled-components';
import Menu from '../../assets/images/menu.svg'
import { device } from "../../assets/theme"

const Button = styled.button`
  background-color:transparent;
  border:0px;
  padding:5px 5px;
  width:50px;
  font-family: 'DM Sans', sans-serif;
  border-radius:20px;
  position:relative;
  outline:none;
  z-index:2;
  cursor:pointer;
  color:${({ theme }) => theme.colors.text};
  
  &.active {
    color:${({ theme }) => theme.colors.background};
  }
`

const Switch = styled.div`
  display:block;
  position:absolute;
  right:15px;
  top:22px;
  padding:3px 5px;
  border-radius:20px;
  border:1px solid ${({ theme }) => theme.colors.preload[0]};
  &:before {
    z-index:0;
    top:4px;
    content:'';
    display:block;
    width:50px;
    height:25px;
    background-color:${({ theme }) => theme.colors.main};
    border-radius:15px;
    position:absolute;
    transition:all 0.25s ease-out;
    left:55px;
  }
  &.left {
    &:before {
      left:5px;
    }
  }
  @media ${device.mobile} {
    display:none;
  }
`
const MobileNativeSelector = styled.div`
  display:none;
  position:absolute;
  right:20px;
  top:28px;
  @media ${device.mobile} {
    display:block;
  }
  img {
    width:7px;
    height:auto;
    filter:${({ theme }) => theme.effect};
  }
  select {
    width:8px;
    appearance:none;
    opacity:0;
    position:absolute;
    right:0px;
    top:0px;
    height:23px;
    .placeholder {
      display:none;
    }
  }
`

export default function Rate({token, changeRate}) { 
  const [rate, setRate] = useState(token);
  
  function add(token) {
    const currency = token === 'USD' ? 'USD' : token;
    setRate(currency);
    changeRate(currency);
  }

  return (
    <>
      <Switch className={rate === token ? '' : 'left'}>
        <Button onClick={() => add('USD')} className={rate === 'USD' ? 'active' : ''}>$</Button>
        <Button onClick={() => add(token)} className={rate === token ? 'active' : ''}>{token}</Button>
      </Switch>
      <MobileNativeSelector>
        <img src={Menu} title="Currency" alt="Currency Options"/>
        <select value={rate} onChange={(e) => add(e.target.value)}>
          <option disabled className="placeholder">Currency </option>
          <option value="USD">USD</option>
          <option value={token}>{token}</option>
        </select>
      </MobileNativeSelector>
    </>
  )
}
