import {m, Porcent} from '../../data/format';
import Network from '../../api/network';
import { device } from "../../assets/theme"
import styled from 'styled-components';

const Text = styled.p`
  min-width:${props => props.raised ? '185px' : "70px"};
  color:${({ theme }) => theme.colors.text};
  @media ${device.tablet} {
    position:absolute;
    right:0px;
    top:-30px;
    text-align:right;
  } 
`;

const Total = styled.strong`
  font-weight:bold;
`

function Raised({api, raised}) {
  const network = Network();
  return (
    <Text raised={raised}>
      <Total> {m(api, network)} </Total> 
      {raised &&  ` | ${Porcent((raised/api) )}% (${m(raised, network)})`}
    </Text>
  )
}

export default Raised;