import { Box, Title } from '../../assets/global'
import ProjectPreloader from '../projects/preloader'
import ProjectsList from '../projects'
import Labels from '../projects/labels'

function Projects({api, parachain, marketshare, globalTvl}) {
  const data = api;
  return !data || !marketshare ? <ProjectPreloader/> : (
    <Box className="full">
      <Title>Crowdloan by Projects</Title>
      <Labels />
      <ProjectsList api={data} parachain={parachain} marketshare={marketshare} globalTvl={globalTvl}/>
    </Box>
  )
}

export default Projects;