import { Box, Title } from '../../assets/global'
import TableHeader from '../dotAuction/tableHeader'
import Table from '../dotAuction/table'
import DotAuctionPreloader from '../dotAuction/dotAuctionPreloader';


function DotAuction({api, unit, parachain , crowdloanRaised}) {
  const totalSupply = unit === 'DOT' ? 1103303471 : 9651217;
  return !api || !crowdloanRaised ? <DotAuctionPreloader/> :(
    <Box>
      <Title>{unit} Auctions</Title>
      <TableHeader />
      <Table api={api} parachain={parachain} unit={unit} crowdloanRaised={crowdloanRaised} totalSupply={totalSupply}/>
    </Box>
  )
}

export default DotAuction;