import Api from '../api'
import _ from "lodash"

export  function  marketDominance () {
  const marketDominanceApi = process.env.REACT_APP_MARKETSHARE
  
  const res  = Api(marketDominanceApi)
  const data = res?.data;
  if(data) {
    return {
      totalValue: data.reduce(function(sum, c) { return sum + c.totalValue }, 0),
      data: data
    }
  }
}
