import { Flex } from '../../assets/global'
import { m , big, Porcent } from '../../data/format'
import {device} from '../../assets/theme'
import styled from 'styled-components';
import dot from '../../assets/images/placeholder/dot.svg';
import ksm from '../../assets/images/placeholder/ksm.svg';

const List = styled.div`
  padding:5px 0px;
  width:25%;
  text-align:right;
  color:${({ theme }) => theme.colors.text};
  @media ${device.mobile} {
    width:60px;
  }
  &:first-of-type {
    padding-top:0px;
    text-align:left;
    @media ${device.mobile} {
      width:85px;
    }
    + div {
      @media ${device.mobile} {
        width:60px;
      } 
      + div {
        @media ${device.mobile} {
          width:80px;
        } 
      }
    }
  }


`;

const Icon = styled.img`
  max-width:35px;
  border-radius:50%;
  display:inline-block;
  vertical-align:middle;
  background-color:${({ theme }) => theme.colors.icon};
  padding:5px;
  + p {
    display:inline-block;
    vertical-align:middle;
    margin-left:10px;
    padding:3px 8px;
    font-size:12px;
    border-radius:10px;
    background:${({ theme }) => theme.colors.status[0]};
    color:${({ theme }) => theme.colors.status[1]};
      @media ${device.mobile} {
        padding:3px 6px;
      }
    }
  }
`

function tabletItem({api, unit, parachain, totalRaised, totalSupply}) {
  const status  = api?.status, 
        raised  = unit === 'KSM' ? parseInt(api?.raised/100): parseInt(api?.raised),
        auction = big(raised)/totalRaised,
        totalSu = big(totalSupply,true),
        total   = raised/totalSu,
        fda     = unit === 'KSM' ? ksm : dot,
        title   = parachain?.icon ? parachain?.name : api?.paraId,
        icon    = parachain?.icon ? parachain?.icon : fda;
  return (
    <>
      <Flex className="spacing">
        <List>
          
          <Icon src={icon} title={title}/>  
          
          { status === 'Won' &&
            <p>{api?.status}</p>
          }
        </List>
        <List>{m(raised)}</List>
        <List>{Porcent(auction)}%</List>
        <List>{Porcent(total)}%</List>
      </Flex>
    </>
  )
}

export default tabletItem;