import { Box } from '../../assets/global'
import styled, { keyframes } from 'styled-components'


const width   = '600px',
      height  = '300px';

const animate = keyframes`
 0%   { background-position: -${width} 0;  }
 100% { background-position: ${width} 0;   }
`

const Preloader = styled.div`
  animation: 1.5s forwards infinite ${animate} linear;
  clip-path: url(#clip);
  -webkit-clip-path: url(#clip-dot);
  background: linear-gradient(to right, ${({ theme }) => theme.colors.preload[0]} 8%, ${({ theme }) => theme.colors.preload[1]} 18%, ${({ theme }) => theme.colors.preload[0]} 33%);
  background-size: ${width} ${height};
  height: 288px;
  -webkit-transform:translateZ(0);
`;

const Svg = styled.svg`
width:0px;
height:0px;
`

const Rect = styled.rect`
`

const DotAuctionPreloader = () => {
  return(
    <Box>
      <Svg height="0" width="0" aria-hidden="true">
        <clipPath id="clip-dot">
          <Rect y="0"   width="200" height="17"/>
          <Rect y="38"  width="1000" height="30"/>
          <circle  cx="20" cy="110" r="15"></circle>
          <Rect y="98" x="60" width="100" height="23"/>
          <Rect y="98" x="180" width="500" height="23"/>
          <circle  cx="20" cy="150" r="15"></circle>
          <Rect y="138" x="60" width="100" height="23"/>
          <Rect y="138" x="180" width="500" height="23"/>      
          <circle  cx="20" cy="190" r="15"></circle>
          <Rect y="178" x="60" width="100" height="23"/>
          <Rect y="178" x="180" width="500" height="23"/>    
          <circle  cx="20" cy="230" r="15"></circle>
          <Rect y="218" x="60" width="100" height="23"/>
          <Rect y="218" x="180" width="500" height="23"/>      
          <circle  cx="20" cy="270" r="15"></circle>
          <Rect y="258" x="60" width="100" height="23"/>
          <Rect y="258" x="180" width="500" height="23"/>                           
        </clipPath>
      </Svg>
      <Preloader/>
    </Box>
  )
}

export default DotAuctionPreloader;