import { Link } from "react-router-dom";
import { useLocation } from "react-router-dom"
import { device }  from '../../assets/theme'
import Network from '../../api/network'
import styled from 'styled-components';

const Nav = styled.nav`
  padding-left:${({ theme }) => theme.padding.middle};
  a {
    font-size:${({ theme }) => theme.font.nav};
    color:${({ theme }) => theme.colors.text};
    padding:${({ theme }) => theme.padding.default};
    &.active {
      background:${({ theme }) => theme.colors.bg};
      border-radius: 100px;
    }
    @media ${device.mobile} {
      &:not(.active) {
        display:none;
      }
    }
  }
`

const List = styled.li`
  display:inline-block;
`

function Links() {
  const location = useLocation()?.pathname;
  const current  = Network()
  return (
    <>
      <Nav>
        <ul>
          <List>
            <Link to={current + '/crowdloan'} className={location.includes('crowdloan') ? 'active' : ''}>Crowdloan</Link>
          </List>
          { 
            /*
            <List>
              <Link to={current + '/money-market'} className={location.includes('money-market') ? 'active' : ''}>Money Market</Link>
            </List>          
            */
          }
        </ul>
      </Nav>
    </>
  )
}

export default Links;