import React from "react";
import { Container , Flex, Grid } from '../assets/global'
import Tvl from '../components/crowdloan/tvl'
import MarketShare from '../components/crowdloan/marketShare'
import CrowdloanTvl from '../components/crowdloan/crowdloanTvl'
import DailyOnParallel from '../components/crowdloan/dailyOnParallel'
import DailyOnCrowdloan from '../components/crowdloan/dailyOnCrowdloan'
import Projects from '../components/crowdloan/projects'
import MarketDominance from '../components/crowdloan/marketDominance'
import DotAuction from '../components/crowdloan/dotAuction'
import Api from '../api/crowdloan/'

function Crowdloan({token}) {
  
  const api = Api({token});

  return (
    <Container>
      { token === 'DOT' &&
        <Flex className="spacing">
          <Tvl          unit={token}      api={api?.tvl} rate={api?.rates}/>
          <MarketShare  unit="PORCENT"    api={api?.share}/>
        </Flex>
      }
      <Flex>
        <Projects 
          api={api?.projects} parachain={api?.parachain} marketshare={api?.marketDominance} globalTvl={api?.globalTvl}/>
      </Flex>     
      { token === 'DOT' &&
        <Flex>
          <DailyOnParallel unit={token}   api={api?.dailyOnParallel} rate={api?.rates}/>
        </Flex>
      }
      <Flex className="spacing">
      { token === 'DOT' &&
        <>
          <MarketDominance api={api?.marketDominance} globalTvl={api?.globalTvl} />
          <Grid>
            <CrowdloanTvl unit={token}     api={api?.contrib} rate={api?.rates}/>
            <DotAuction api={api?.projects} parachain={api?.parachain} crowdloanRaised={api?.globalTvl} unit={token}/>
          </Grid>
        </>
      }
      { token === 'KSM' &&  
        <>
          <CrowdloanTvl unit={token}     api={api?.contrib} rate={api?.rates}/>
          <DotAuction api={api?.projects} parachain={api?.parachain} crowdloanRaised={api?.globalTvl} unit={token}/>
        </>
      }
      </Flex>
      
      <Flex>
        <DailyOnCrowdloan unit={token} parachain={api?.parachain} api={api?.dailyOnCrowdloan} rate={api?.rates}/>
      </Flex>      
   
    </Container>
  );
}

export default Crowdloan;