export function m (value,network,withoutDecimals) {
  
  const decimals = network === 'kusama' ? 1000000000000 : 10000000000
  const val = withoutDecimals ? value : value/decimals;
    return Math.abs(Number(val)) >= 1.0e+9 ? 
    (Math.abs(Number(val)) / 1.0e+9).toFixed(2) + "B"
    // Six Zeroes for Millions 
    : Math.abs(Number(val)) >= 1.0e+6

    ? (Math.abs(Number(val)) / 1.0e+6).toFixed(2) + "M"
    // Three Zeroes for Thousands
    : Math.abs(Number(val)) >= 1.0e+3

    ? (Math.abs(Number(val)) / 1.0e+3).toFixed(1) + "K"

    : Math.abs(Number(val)).toFixed(1);

}

export function n (value) {
  return parseFloat(value?.toFixed(2)).toLocaleString("en-US");
}

export function date (date) {
  return date.substring(0,6);
}

export function big (number, activate) {
  return activate ? number*10000000000: number/10000000000;
}

export function bigk(number, activate) {
  return activate ? number*1000000000000: number/1000000000000;
}

export function Porcent (value) {
  return n(value*100)
}

export function DOT (value) {
  return n(value) + ' DOT '
}

export function KSM (value) {
  return n(value) + ' KSM '
}

export function USD (value) {
  return '$' + n(value) + ' '
}

export function P (value) {
  return n(value)+ '% ' 
}

export function Val({value, unit}){
  return (
    (() => {
      switch (unit) {
        case 'PORCENT' :
          return(P(value))
        case 'DOT' :
          return(DOT(value))
        case 'KSM' :
          return(KSM(value))
        default:
          return(USD(value))
      }
    })()
  )
}