import React from "react";
import { ThemeProvider } from "styled-components";
import { BrowserRouter as Router, Routes, Navigate, Route } from "react-router-dom";
import { lightTheme, darkTheme } from "./assets/theme";
import useDarkTheme from "./assets/useDarkTheme";
import { GlobalStyles } from "./assets/styles/globalStyles";

import Polkadot from './networks/polkadot'
import Kusama from './networks/kusama'
import Header from './components/navigation/header'

function App() {
  const [theme, themeToggler] = useDarkTheme();
  const selectedTheme = theme === 'light' ? lightTheme : darkTheme;
  
  return (
    <Router>
      <ThemeProvider theme={selectedTheme}>
        <Header themeToggler={themeToggler} />
        <Routes>
          <Route path="/"                         element={<Navigate replace to="/polkadot/crowdloan" /> } />          
          <Route index path='/polkadot/*'         element={<Polkadot /> } />
          <Route path='/kusama/*'                 element={<Kusama   /> } />
        </Routes>
        <GlobalStyles/>
      </ThemeProvider>
    </Router>
  );
}

export default App;
