import { BarChart, Bar, XAxis, Tooltip, ResponsiveContainer } from 'recharts';
import Legend from './legend'

const ChartBar = ({ data, unit , type }) => {
  return(
    <ResponsiveContainer width="100%" height={200} >
      <BarChart width={600} height={200} data={data} margin='0'>
        <XAxis dataKey="date" interval={type === 'small' ? 11 : 30}  textAnchor="left" />
        <Tooltip content={<Legend unit={unit}/>} />
        <Bar dataKey="val" fill={"#0C3EE3"} radius={[4, 4, 0, 0]} animationDuration={250}/>
      </BarChart>
    </ResponsiveContainer>    
  )
}

export default ChartBar;