import { htvl } from './htvl'
import { contributions } from './contributions'
import { marketShare } from './marketShare'
import { parachains } from './parachains'
import { projects } from './projects'
import { marketDominance } from './marketDominance'
import { rate } from '../rate'

const  Api = ({token}) => {
  const tvl           = htvl(),
        project       = projects(),
        parachain     = parachains(),
        contrib       = contributions(),
        share         = marketShare(tvl?.tvl,contrib?.contributions),
        dominance     = marketDominance(),
        rates         = rate(token);

  return {
    tvl               : tvl?.tvl,
    projects          : project,
    contrib           : contrib?.contributions,
    share             : share,
    marketDominance   : dominance,
    dailyOnParallel   : tvl?.original,
    dailyOnCrowdloan  : contrib?.original,
    parachain         : parachain,
    globalTvl         : contrib?.contributions ? contrib?.contributions[contrib?.contributions.length-1].val : 0,
    rates             : rates
  }
  
}

export default Api
