import Api from '../api'
import Network from '../../api/network';

export  function  projects () {
  const projects_api = Network() === 'kusama' ? 
  process.env.REACT_APP_KUSAMA_RAISED
  : 
  process.env.REACT_APP_POLKADOT_RAISED;
  
  const res  = Api(projects_api)
  const data = res?.data?.filter(a => a.status !== "Expired")?.sort((a, b) => b.raised - a.raised)
  
  if(data) {
    return data;
  }
}
