import React, { useState } from 'react';
import { useTheme } from 'styled-components';
import styled from 'styled-components';
import Select from 'react-select';
import Menu from '../../assets/images/menu.svg'
import { device } from "../../assets/theme"


const FilterContainer = styled.div`
  position:absolute;
  right:25px;
  width:200px;
  top:20px;
  cursor:pointer;
  color:${({ theme }) => theme.colors.text};
  @media ${device.mobile} {
    display:none;
  }
`
const Filter = styled(Select)``;

const MobileNativeSelector = styled.div`
  display:none;
  position:absolute;
  right:20px;
  top:30px;
  @media ${device.mobile} {
    display:block;
  }
  img {
    width:7px;
    height:auto;
    filter:${({ theme }) => theme.effect};
  }
  select {
    width:8px;
    appearance:none;
    opacity:0;
    position:absolute;
    right:0px;
    top:0px;
    height:23px;
    .placeholder {
      display:none;
    }
  }
`

export default function FilterByProject({type, setType, parachain}) { 
const theme = useTheme();

const customStyles = {
  control: base => ({
    ...base,
    background: theme?.colors?.bg,
    borderColor:theme?.colors?.preload[0],
    "&:hover": {
      borderColor: theme?.colors?.preload[1],
      color: theme?.colors?.text
    }
  }),
  dropdownIndicator: base => ({
    ...base,
    color: theme?.colors?.text,
    background: theme?.colors?.bg
  }),
  singleValue: base => ({
    ...base,
    background: theme?.colors?.bg,
    color: theme?.colors?.text
  }),
  option: (provided, state) => ({
    ...provided,
    border:0,
    outline:'none',
    color: theme?.colors?.text,
    padding: 10,
    background: theme?.colors?.bg,
    "&:hover": {
      backgroundColor: theme?.colors?.background,
      color: theme?.colors?.text
    }
  }),
}

  const types = parachain?.map((item) => {
    return {
      value:item.name,
      label:item.name,
    }
  });

  return (
    <>
      <FilterContainer>
        <Filter
          styles={customStyles}
          options={types} 
          placeholder={type}
          onChange={(opt) => [setType(opt.value)]}
        />
      </FilterContainer>
      <MobileNativeSelector>
        <img src={Menu} title="Select a Project" alt="Select a Project"/>
        <select value={type} onChange={(e) => setType(e.target.value)}>
          <option disabled className="placeholder">Select a Project </option>
          {
            parachain?.map((item,key) => {
              return <option key={key} value={item?.name}>{item?.name}</option>
            })
          }
        </select>
      </MobileNativeSelector>    
    </>
  )
}
